@use 'base/variables' as v;
@use 'base/mixins' as m;
@use "sass:color";

.barra {
    background-color: v.$blanco;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px v.$gris;
    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    p {
        margin: 0;
        font-size: 1.4rem;
        span {
            font-weight: 700;
            color: v.$indigo;
        }
    }

    .cerrar-sesion {
        font-size: 1.4rem;
        padding: 1rem;
        border-radius: .5rem;

        @include m.boton (color.adjust(v.$indigo, $lightness: -20%), v.$blanco);
    }        
}