@use 'base/variables' as v;
@use 'base/mixins' as m;
@use "sass:color";

.formulario {
    margin-top: 5rem;
    .campo {
        display: flex;
        align-items: center;
        margin-bottom: 2rem;
        label {
            flex: 0 0 10rem;
        }
    
        input {
            flex: 1; // flex: 1 1 0%;
            padding: 1rem;
            border: none;
            background-color: color.adjust(v.$gris, $lightness: 40%);

        }
    }
}